import cookie from "vue-cookies"
export default {
  state: {
    user: (cookie.get("myuser")==null?"": (cookie.get("myuser"))), // 登录的用户
    userId:(cookie.get("UserId")==null?"": (cookie.get("UserId"))), // 登录的用户
    showLogin: false // 用于控制是否显示登录组件
  },
  getters: {
    getUser (state) {
      return state.user
    },
    getUserId (state) {
      return state.userId
    },
    getShowLogin (state) {
      return state.showLogin
    }
  },
  mutations: {
    setUser (state, data) {
      state.user = data;
      console.log("user",state.user);
    },
    setuserId (state, data) {
      state.userId = data;
      console.log("UserId",state.userId);
    },
    setShowLogin (state, data) {
      state.showLogin = data;
      
    }
  },
  actions: {
    setUser ({ commit }, data) {
      commit('setUser', data);
    },
    setuserId ({ commit }, data) {
      commit('setuserId', data);
    },
    setShowLogin ({ commit }, data) {
      commit('setShowLogin', data);
    }
  }
}