 import cookie from "vue-cookies"
//准备state——用于存储数据
const state = {

 
  
}

export default {
  state,
  getters: {
    getShoppingCart (state) {
      // 获取购物车状态
      return  (state.shoppingCart);
    },
    checkShoppingCart (state,data) {
      // 获取购物车状态
      for (let i = 0; i < state.shoppingCart.length; i++) {
        if(state.shoppingCart.product_id ==data)
        return false 
      } 
      return true
    },
    getOrder (state) {
      // 获取购物车状态
      return  (state.Order);
    },
    getdetil(state ){
      return state.detil;
    },
    getCollect (state) {
       return state.Collect;
    },
    // getNum (state) {
    //   // 购物车商品总数量
    //   let totalNum = 0;
    //   if(state.shoppingCart.length === null)
    //   return 0;
    //   for (let i = 0; i < state.shoppingCart.length; i++) {
    //     const temp = state.shoppingCart[i];
    //     totalNum += temp.count;
    //   }      

    //   return totalNum;
    // },
    getIsAllCheck (state) {
      // 判断是否全选
      let isAllCheck = true;
      for (let i = 0; i < state.shoppingCart.length; i++) {
        const temp = state.shoppingCart[i];
        // 只要有一个商品没有勾选立即return false;
        if (!temp.check) {
          isAllCheck = false;
          return isAllCheck;
        }
      }
      return isAllCheck;
    },
    getCheckGoods (state) {
      // 获取勾选的商品信息
      // 用于确认订单页面
      let checkGoods = [];
      for (let i = 0; i < state.shoppingCart.length; i++) {
        const temp = state.shoppingCart[i];
        if (temp.check) {
          checkGoods.push(temp);
        }
      }
      return checkGoods;
    },
    getCheckNum (state) {
      // 获取购物车勾选的商品数量
      let totalNum = 0;
      for (let i = 0; i < state.shoppingCart.length; i++) {
        const temp = state.shoppingCart[i];
        console.log(temp.check,temp);
         if (temp.check) {
          totalNum += temp.count;
         }
      }
      return totalNum;
    },
    getTotalPrice (state) {
      // 购物车勾选的商品总价格
      let totalPrice = 0;
      for (let i = 0; i < state.shoppingCart.length; i++) {
        const temp = state.shoppingCart[i];
      if (temp.check) {
          totalPrice += temp.product_price * temp.count;
        }
      }
      return totalPrice;
    },
    getItem (state, data) {
      // 点击全选按钮，更改每个商品的勾选状态
      console.log("选按钮");
      for (let i = 0; i < state.shoppingCart.length; i++) {
        console.log(state.shoppingCart[i]);
        if(data == state.shoppingCart[i].product_id)
        return state.shoppingCart[i]

      }
      return 0
    }
  },
  mutations: {
    setShoppingCart (state, data) {
      // 设置购物车状态      
      // var cart =JSON.stringify(data)
      // cookie.set("shoppingCart",cart)
      // state.shoppingCart = cookie.get("shoppingCart");
      console.log("设置购物车状态",state.shoppingCart,data);
    },
    addcounr (state, data) {
      for (let i = 0; i < state.shoppingCart.length; i++) {
        if(state.shoppingCart.product_id ==data)
        state.shoppingCart.count++;
      }  
    },
    unshiftShoppingCart (state, data) {
      // 添加购物车
      // 用于在商品详情页点击添加购物车,后台添加成功后，更新vuex状态
      let totalNum = 0;
      if(state.shoppingCart.length === null)
      return 0;
      for (let i = 0; i < state.shoppingCart.length; i++) {
        const temp = state.shoppingCart[i];
        totalNum += temp.count;
      }      
      data["cartid"]=totalNum+1
 
      state.shoppingCart.push(data)
      var cart = JSON.stringify(state.shoppingCart);
      cookie.set("shoppingCart",cart)
      console.log("shoppingCart",cookie.get("shoppingCart"));
      console.log("shoppingCart",state.shoppingCart);
    },
    unshiftCollent (state, data) {
      // 添加收藏
      // 用于在商品详情页点击添加购物车,后台添加成功后，更新vuex状态
      console.log("后台添加成功");
      state.Collect.unshift(data)
      var cart = JSON.stringify(state.Collect);
      cookie.set("collect",cart)
     
      console.log(state.Collect);
    },
    updateShoppingCart (state, payload) {
      // 更新购物车
      // 可更新商品数量和是否勾选
      // 用于购物车点击勾选及加减商品数量
      if (payload.prop == "count") {
        // 判断效果的商品数量是否大于限购数量或小于1
        if (state.shoppingCart[payload.key].maxNum < payload.val) {
          return;
        }
        if (payload.val < 1) {
          return;
        }
      }
      // 根据商品在购物车的数组的索引和属性更改
      state.shoppingCart[payload.key][payload.prop] = payload.val;
      console.log("索引和属性",state.shoppingCart[payload.key][payload.prop],payload.key,payload.prop);
      var cart = JSON.stringify(state.shoppingCart);
      cookie.set("shoppingCart",cart)
    },
    addShoppingCartNum (state, productID) {
      // 增加购物车商品数量
      // 用于在商品详情页点击添加购物车,后台返回002，“该商品已在购物车，数量 +1”，更新vuex的商品数量
      if(state.shoppingCart.length === null)
      {console.log(null);}
      else{
      for (let i = 0; i < state.shoppingCart.length; i++) {
        const temp = state.shoppingCart[i];
        if (temp.productID == productID) {
          if (temp.num < temp.maxNum) {
            temp.num++;
          }
        }
      }
    }
    },
    deleteShoppingCart (state, index) {
      state.shoppingCart.splice(index, 1);
      var cart = JSON.stringify(state.shoppingCart);
      cookie.set("shoppingCart",cart)
      // // 根据购物车id删除购物车商品
      // for (let i = 0; i < state.shoppingCart.length; i++) {
      //   const temp = state.shoppingCart[i];
      //   if (temp.id == id) {
      //     state.shoppingCart.splice(i, 1);
      //   }
      // }
    },
    checkAll (state, data) {
      // 点击全选按钮，更改每个商品的勾选状态
      for (let i = 0; i < state.shoppingCart.length; i++) {
        state.shoppingCart[i].check = data;
      }
    },
    setdetil (state, data) {
      // 点击全选按钮，更改每个商品的勾选状态
      console.log("品的勾选");
      for (let i = 0; i < state.shoppingCart.length; i++) {
        console.log("共和国和",state.shoppingCart[i]);
        if(state.shoppingCart[i].product_id == data){
          console.log("国际控股");
          state.detil=state.shoppingCart[i]
        }
      }
      console.log(state.detil);
    },
    setOrder(state, data){
      state.Order.unshift(data);
      console.log("订单",state.Order);
      var cart = JSON.stringify(state.Order);
      cookie.set("order",cart)


    }
  },
  actions: {
    setShoppingCart ({ commit }, data) {
      commit('setShoppingCart', data);
    },
    setOrder ({ commit }, data) {
      commit('setOrder', data);
    },
    addcounr ({ commit }, data) {
      commit('addcounr', data);
    },
    setdetil ({ commit }, data) {
      commit('setdetil', data);
    },
    unshiftCollent ({ commit }, data) {
      commit('unshiftCollent', data);
    },
    unshiftShoppingCart ({ commit }, data) {
      commit('unshiftShoppingCart', data);
    },
    updateShoppingCart ({ commit }, payload) {
      commit('updateShoppingCart', payload);
    },
    addShoppingCartNum ({ commit }, productID) {
      commit('addShoppingCartNum', productID);
    },
    deleteShoppingCart ({ commit }, index) {
      commit('deleteShoppingCart', index);
    },
    checkAll ({ commit }, data) {
      commit('checkAll', data);
    }
  }
}